
import { defineComponent, PropType, toRefs, computed } from "vue";
import { splitAt } from "ramda";
import { Column } from "@/constants/element";

const Component = defineComponent({
  name: "TableCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    columns: {
      type: Array as PropType<Column[]>,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const { data } = toRefs(props);
    const firstData = computed(() => splitAt(5, data.value)[0]);
    const lastData = computed(() => splitAt(5, data.value)[1]);

    return {
      firstData,
      lastData
    };
  }
});

export default Component;
