<template>
  <div id="table-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
    </div>
    <el-row>
      <el-col :span="12">
        <div class="table-container">
          <el-table
            :data="firstData"
            :style="{ width: 'calc(100% - 60px)' }"
            row-class-name="table-row"
            cell-class-name="table-cell"
          >
            <el-table-column
              v-for="column in columns"
              :key="column.prop"
              :label="column.label"
              :width="column.width || 'auto'"
            >
              <template #default="scope">
                <p>{{ scope.row[column.prop] }}</p>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="table-container">
          <el-table
            :data="lastData"
            :style="{ width: 'calc(100% - 60px)' }"
            row-class-name="table-row"
            cell-class-name="table-cell"
          >
            <el-table-column
              v-for="column in columns"
              :key="column.prop"
              :label="column.label"
              :width="column.width || 'auto'"
            >
              <template #default="scope">
                <p>{{ scope.row[column.prop] }}</p>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, toRefs, computed } from "vue";
import { splitAt } from "ramda";
import { Column } from "@/constants/element";

const Component = defineComponent({
  name: "TableCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    columns: {
      type: Array as PropType<Column[]>,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const { data } = toRefs(props);
    const firstData = computed(() => splitAt(5, data.value)[0]);
    const lastData = computed(() => splitAt(5, data.value)[1]);

    return {
      firstData,
      lastData
    };
  }
});

export default Component;
</script>

