<template>
  <div id="fabrics-analysis-component">
    <div class="table-wrapper">
      <h2 class="zoom-title">Recent View</h2>
      <div class="row">
        <div class="col" v-for="(item, index) in data.recentView" :key="index">
          <FabricCard :data="item"></FabricCard>
          <p class="view-note">
            last viewed {{ getLastViewTimeString(item.datetime) }} ago
          </p>
        </div>
      </div>
    </div>
    <div class="table-wrapper">
      <h2 class="zoom-title">Company List</h2>
      <TableTwoColumnCard
        :columns="columns"
        :data="data.companyRank"
      ></TableTwoColumnCard>
    </div>
    <div class="table-wrapper">
      <h2 class="zoom-title">Fabric Rankings</h2>
      <TableCard
        :columns="collectionRankingsColumns"
        :data="data.objectRank"
      ></TableCard>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, computed } from "vue";
import FabricCard from "@/components/FabricCard.vue";
import TableTwoColumnCard from "@/components/TableTwoColumnCard.vue";
import TableCard from "@/components/TableCard.vue";
import { Column } from "@/constants/element";
import { useStore } from "vuex";
import {
  GetFabricAnalyzeCompanyRank,
  GetFabricAnalyzeObjectRank,
  GetFabricAnalyzeRecentView
} from "@/typings/store";
import { getLastViewTimeString, loading } from "@/utils";

const columns: Column[] = [
  { prop: "rank", label: "Rankings" },
  { prop: "company", label: "Company" },
  { prop: "count", label: "Click" }
];

const collectionRankingsColumns: Column[] = [
  // { prop: "rank", label: "Rankings", width: "90px" },
  { prop: "name", label: "Name" },
  { prop: "fabric", label: "Fabric", width: "120px" },
  // { prop: "color", label: "Color" },
  { prop: "real_composition", label: "Real Composition", width: "250px" },
  { prop: "count", label: "Click", width: "100px", sort: true },
  { prop: "ctr", label: "CTR", width: "100px", sort: true }
];

const Component = defineComponent({
  name: "FabricsAnalysis",
  components: {
    FabricCard,
    TableCard,
    TableTwoColumnCard
  },
  setup() {
    const store = useStore();

    const data = computed(() => store.state.fabric.analysis);

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetFabricAnalyzeRecentView);
        await store.dispatch(GetFabricAnalyzeCompanyRank);
        await store.dispatch(GetFabricAnalyzeObjectRank);
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    return {
      data,
      columns,
      collectionRankingsColumns,

      getLastViewTimeString
    };
  }
});

export default Component;
</script>

